.contact__container {
  grid-template-columns: repeat(2, max-content);
  justify-content: center;
  column-gap: 6rem;
  padding-bottom: 3rem;
}

.contact__title {
  text-align: center;
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1-5);
}

.contact__info {
  display: grid;
  row-gap: 1rem;
  grid-template-columns: 300px;
}

.contact__card {
  background-color: var(--container-color);
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 0.75rem;
  text-align: center;
}

.contact__card-icon {
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-0-25);
}

.contact__card-title,
.contact__card-data {
  font-size: var(--small-font-size);
}


.contact__card-title{
    font-weight: var(--font-medium);
}

.contact__card-data
{
    display: block;
    margin-bottom: var(--mb-0-75);
    user-select: text !important;
}

.contact__button
{
    color: var(--text-color);
    font-size: var(--small-font-size);
    display: inline-block;
    align-items: center;
    justify-content: center;
    column-gap: .25rem;
}

.contact__button-icon
{
    font-size: 1rem;
    transition: .3s;
}

.contact__button:hover .contact__button-icon
{
    transform: translate(00.25rem);
}

.contact__form {
  empty-cells: 360px;
  user-select: text;
}

.contact__form-div
{
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 0.3);
    background: none;
    color: var(--text-color);
    outline: none !important;
    border-radius: 0.3rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact__form-tag
{
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    color: var(--title-color);
    font-weight: var(--font-medium);
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}

.contact__form-area
{
    height: 11.3rem;
}

.contact__form-area textarea
{
    resize: none;
}
.full
{
    width: 100%;
    justify-content: center;
    /* font-size: var(--medium-font-size); */

}



/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    
    .contact__container{
        column-gap: 3rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
   .contact__container
   {
    grid-template-columns: 1fr;
    row-gap: 3rem;
   }

   .contact__info{
    justify-content: center;
   }

   .contact__form
   {
    margin: 0 auto;
   }
  }
  
  @media screen and (max-width: 576px) {
    .contact__info
    {
        grid-template-columns: 1fr;
    }

    .contact__form{
        width: 100%;
    }
  }
  
